import React, { useState } from 'react'
import TokenDescription from './TokenDescription'
import useLocale from 'utils/hooks/useLocale'

const Create = () => {
    let language = useLocale();
    console.log("In create, The language : " + language)

    const moduleTypes =
        [
            {
                MODULE: language !== "CN" ? "Normal Token" : "普通代幣",
            },
            {
                MODULE: language !== "CN" ? "Marketing && LP Token" : "營銷回流代幣",
            },
            {
                MODULE: language !== "CN" ? "Reward Token" : "分紅代幣",
            },
        ]

    const [name, setName] = useState('');
    const [symbol, setSymbol] = useState('');
    const [decimal, setDecimal] = useState('');
    const [totalSupply, setTotalSupply] = useState('');

    const [isOpen, setIsOpen] = useState(false);
    const [selectedValue, setSelectedValue] = useState(moduleTypes[0].MODULE);

    const styles = {
        paddingLeft: '40px',
        height: '80px',
        width: '80%'
    }

    const styles2 = {
        border: '1px solid black'
    }

    const submitValue = (e) => {
        e.preventDefault();
        console.log(`
        ==== Values ====
        Name : ${name}
        Symbol : ${symbol}
        Decimal : ${decimal}
        Total Supply : ${totalSupply}
        `)
    }

    const texts = {
        title: language !== "CN" ? "Token Creator" : "一鍵發幣",
        module: language !== "CN" ? "Token Type" : "請選擇合約模板",
        description: language !== "CN" ? "Token Description" : "代幣說明",
    }

    const datas = [
        {
            title: language !== "CN" ? "Token Name" : "代幣名稱",
            value: name,
            textType: "text",
            function: e => setName(e.target.value),
            style: styles2,
            placeholder: "Ethereum",
        },
        {
            title: language !== "CN" ? "Token Symbol" : "代幣縮寫",
            value: symbol,
            textType: "text",
            function: e => setSymbol(e.target.value),
            style: styles2,
            placeholder: "Eth",
        },
        {
            title: language !== "CN" ? "Token Decimal" : "代幣精度",
            value: decimal,
            textType: "number",
            function: e => setDecimal(e.target.value),
            style: styles2,
            placeholder: "18",
        },
        {
            title: language !== "CN" ? "Token Supply" : "代幣供應量",
            value: totalSupply,
            textType: "number",
            function: e => setTotalSupply(e.target.value),
            style: styles2,
            placeholder: "1000000",
        },
    ]

    const toggleDropdown = () => {
        setIsOpen(!isOpen);
    };

    const handleOptionSelect = (value) => {
        setSelectedValue(value);
        setIsOpen(false);
        console.log(value);
    };

    return (
        <div style={{
            width: '100%',
            color: 'black',
            position: 'absolute',
            height: '85vh',
            overflowY: 'scroll',
            paddingLeft: '20px'
        }}>
            <div style={{ textAlign: 'left', }}>
                <h1>{texts.title}</h1><br />
            </div>
            <div>
                <h5 style={{
                    justifyContent: 'space-between',
                    display: 'flex',
                    flexDirection: 'row',
                    width: '90%'
                }}>
                    <div>
                        {texts.module}
                    </div>
                </h5>
                <div style={{ display: 'flex', flexDirection: 'row' }}>
                    <span
                        onClick={toggleDropdown}
                        style={{
                            marginLeft: '5vw',
                            backgroundColor: 'lightgray',
                            border: '1px solid black',
                            borderRadius: '30px',
                            cursor: 'pointer',
                            padding: '10px 30px',
                            width: '200px',
                            justifyContent: 'space-between',
                            display: 'flex',
                            flexDirection: 'row',
                            fontWeight: 'bold',
                            alignItems: 'center',
                        }}>
                        <div>{selectedValue || texts.module}</div><div> ▼ </div>
                    </span>

                    <div style={{
                        padding: '10px 30px',
                    }}>
                        {texts.description}
                    </div>
                </div>
            </div>
            {
                isOpen && (
                    <ul style={{ marginLeft: '6vw' }}>
                        {
                            moduleTypes.map((module, index) => {
                                return (
                                    <li
                                        onClick={() => handleOptionSelect(`${module.MODULE}`)}
                                        key={index}
                                    >{module.MODULE}</li>
                                )
                            })
                        }
                    </ul>
                )
            }
            <br />

            <div
                className="createContractWrapper"
                style={{
                    borderTop: '1px solid black',
                    borderBottom: '1px solid black',
                    // borderRadius: '10px',
                    marginRight: '10vw',
                }}>

                {
                    datas.map((data, index) => {
                        return (
                            <div key={index} style={styles}>
                                <h4>{data.title}</h4>
                                <input
                                    text={data.textType}
                                    value={data.value}
                                    onChange={data.function}
                                    style={data.style}
                                    placeholder={data.placeholder}
                                />
                            </div>
                        )
                    })
                }
            </div>

            <button onClick={submitValue} style={styles2}>Submit</button>
        </div>
    )
}

export default Create
